import { createStore } from 'vuex'

export default createStore({
  state() {
    return {
      sidebarVisible: '',
      sidebarUnfoldable: false,
      post: {},
    };
  },
  getters: {
    getAllPost(state){
      console.log(state.post);
      return state.post;
    }
  },
  mutations: {
    toggleSidebar(state) {
      state.sidebarVisible = !state.sidebarVisible
    },
    toggleUnfoldable(state) {
      state.sidebarUnfoldable = !state.sidebarUnfoldable
    },
    updateSidebarVisible(state, payload) {
      state.sidebarVisible = payload.value
    },
    getPost(state, payload){
      state.post = payload
    }
  },
  actions: {
    async getAllPost({commit}){
      const response = await fetch('https://script.google.com/macros/s/AKfycbwo_TI09KsJDIe9SS0d9UcJNe-l49p5U5QHs2yYhKpidablaHEAKnToilY1_5PjcROG/exec?action=read&table=post');
      const result = await response;
      console.log(result);

      commit('getPost', result);
    }
  },
  modules: {},
})
